import { arrayOf, func, shape, string } from 'prop-types';
import IconTile from './IconTile';
import styled from 'styled-components';
import { mapKeys, map, reduce } from 'lodash/fp';
import mediaQuery from '../utils/mediaQuery';
import theme from '../theme';
import { animations } from './animations';



function IconGrid({ icons }) {
  const animationsWithSections = reduce((acc, item) => {
    if (!acc[item.section]) {
      acc[item.section] = {
        name: item.section,
        icons: [],
      };
    }

    acc[item.section].icons.push(item);
    return acc;
  }, {})(animations);

  return map(section => {
    return (
      <SectionWrapper>
        <SectionTitle>{section.name}</SectionTitle>
        <div
          css={{
            display: 'grid',
            gridTemplateColumns: 'repeat(auto-fill, minmax(100px, 1fr))',
            rowGap: theme.space[5],
            columnGap: theme.space[4],
            margin: '0 auto',
            [mediaQuery(theme.breakpoints.large)]: {
              columnGap: theme.space[5],
            },
          }}
        >
          {section.icons.map(animation => (
            <IconTile key={animations.animationKey} {...animation} />
          ))}
        </div>
      </SectionWrapper>
    );
  }, animationsWithSections);
}


const SectionWrapper = styled.div`
  margin: 56px 0;
`

IconGrid.propTypes = {
  icons: arrayOf(
    shape({
      name: string,
      toSvg: func,
    })
  ).isRequired,
};

const SectionTitle = styled.div`
  margin-bottom: 32px;
  font-size: 32px;
  line-height: 44px;
  font-weight: 400;
`;

export default IconGrid;
