import { rgba } from 'polished'
import styled from 'styled-components'
import UseAnimations from 'react-useanimations'
import { string, func } from 'prop-types'
import theme from '../theme'

function IconTile({ animationKey, effect, ...props }) {
  return (
    <div
      role="button"
      tabIndex={0}
      css={{
        display: 'flex',
        flexDirection: 'column',
        textAlign: 'center',
        alignItems: 'stretch',
        cursor: 'pointer',
        outline: 0,
        '&:hover .icon-container': {
          color: theme.colors.primary[0],
          boxShadow: theme.shadows[2],
        },
        '&:focus .icon-container': {
          boxShadow: `0 0 0 3px ${rgba(theme.colors.primary[0], 0.5)}`,
        },
      }}
      {...props}
    >
      <div
        className="icon-container"
        css={{
          width: '100%',
          padding: '24px 0',
          backgroundColor: theme.colors.white,
          borderRadius: 4,
          marginBottom: 8,
          boxShadow: theme.shadows[1],
        }}
      >
        <Animation animationKey={animationKey} effect={effect} />
      </div>
      <span css={{ fontSize: theme.fontSizes[1], color: theme.colors.gray[8] }}>
        {animationKey}
      </span>
    </div>
  )
}

IconTile.propTypes = {
  name: string.isRequired,
  onClick: func.isRequired,
}

export default IconTile

const Animation = styled(UseAnimations)`
  width: 24px;
  height: 24px;
  margin: 0 auto;
`
